.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.5);
  backdrop-filter: blur(4px);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 99999;
  padding: 1rem;
}

.contacts-modal {
  max-width: 800px;
  width: 100%;
}

.modal-container {
  border-radius: 16px !important;
  background: white;
  border-radius: 12px;
  position: relative;
  box-shadow: 0 20px 25px -5px rgba(0, 0, 0, 0.1), 0 10px 10px -5px rgba(0, 0, 0, 0.04);
  animation: modalSlideIn 0.3s ease-out;
}

@keyframes modalSlideIn {
  from {
    opacity: 0;
    transform: scale(0.95) translateY(-10px);
  }
  to {
    opacity: 1;
    transform: scale(1) translateY(0);
  }
}

.close-btn {
  position: absolute;
  top: 1rem;
  right: 1rem;
  background: none;
  border: none;
  cursor: pointer;
  padding: 0.5rem;
  border-radius: 50%;
  transition: background-color 0.2s;
  z-index: 1;
  color: white;
}

.close-btn:hover {
  background-color: rgba(255, 255, 255, 0.1);
}

.modal-header {
  border-radius: 16px 16px 0 0 !important;
  padding: 1.5rem 1.5rem 1rem;
  border-bottom: 1px solid #e5e7eb;
  background: #D4AF5F !important;
  color: #5F646D;
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: row;
}

.modal-header .modal-title {
  display: flex;
  align-items: center;
  justify-content: flex-start;
}

.modal-header .ltr{
  justify-content: flex-start;
}
.modal-title {
  margin: 0;
  font-size: 1.25rem;
  font-weight: 600;
  color: #5F646D;
}

.modal-body {
  padding: 1.5rem;
}

.modal-footer {
  padding: 1rem 1.5rem 1.5rem;
  border-top: 1px solid #e5e7eb;
}

.contact-row {
  background: #f9fafb;
  border-color: #e5e7eb !important;
}

.contact-row:hover {
  background: #f3f4f6;
}

.btn-sm {
  padding: 0.375rem 0.75rem;
  font-size: 0.875rem;
  font-weight: 600;
  border-radius: 6px;
  transition: all 0.2s ease;
}

.btn-success {
  color: white !important;
  background-color: #059669;
  border-color: #059669;
}

.btn-success:hover {
  background-color: #047857;
  border-color: #047857;
  color: white !important;
}

.btn-danger {
  color: white !important;
  background-color: #dc2626;
  border-color: #dc2626;
}

.btn-danger:hover {
  background-color: #b91c1c;
  border-color: #b91c1c;
  color: white !important;
}

/* Responsive */
@media (max-width: 768px) {
  .contacts-modal {
    margin: 1rem;
    max-width: calc(100vw - 2rem);
  }
  
  .contact-row .row {
    flex-direction: column;
  }
  
  .contact-row .col-md-2 {
  margin-top: 0.5rem;
  justify-content: center;
}

.contact-row .btn {
  min-width: 36px;
  height: 36px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.contact-row .btn i {
  font-size: 0.875rem;
}
}
